.menu {
  background-color: $colorGrey;

  &__top {
    @include top-actions;
    margin-bottom: 44px;
  }

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: 30px 0;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    text-decoration: none;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $colorDark-Main;
    transition: border-color 0.5s;

    &:hover {
      border-color: $colorDark-Main;
    }
  }

  &__call {
    width: max-content;
    display: block;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    color: $colorDark-Main;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $colorDark-Main;
      letter-spacing: 2px;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 12px;
    }
  }
}
